body {
  touch-action: manipulation;
  background: linear-gradient(0deg, #04022a 0%, #272761 29%, #02023c 100%);
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

#game-board {
  border: 3px solid #ff0;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

#card-root {
  justify-content: center;
  align-items: center;
  width: 5em;
  height: 7em;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#card {
  pointer-events: none;
  -webkit-user-select: none;
  user-select: none;
  width: 5em;
  height: 7em;
  position: absolute;
}

#card-item {
  filter: drop-shadow(-5px 5px 5px #000);
  width: 100%;
  height: 100%;
  position: relative;
}

.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.card-illustration {
  width: 90%;
  height: auto;
  max-height: 50%;
  position: absolute;
  top: 33%;
  left: 50%;
  transform: translate(-50%, -50%);
}

